<template>
    <div class="lock-screen">
        <div class="lock-wrapper col-sm-4 col-xs-12">
            <div class="panel lock-box text-center">
                <div class="locked">
                    <i class="fa fa-lock"></i>
                </div>
                <h1>微车邮箱登录</h1>
                <div class="row">
                    <form action="index.html" class="form-inline" role="form">
                        <div class="form-group col-md-12 col-sm-12 col-xs-12">
                            <input type="email" required v-model="email" class="form-control lock-input" placeholder="请输入微车邮箱">
                            <button type="submit" @click.stop.prevent="login" class="btn btn-lock pull-right">
                                <i class="fa fa-check"></i>
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>
<script type="text/javascript">
import CommonTool from '../../Index/Lib/CommonTool.js'
export default {
    data () {
        return {
            email: window.localStorage.getItem('weiche_cms_email'),
            api: '/weiche_cms/api/'
        }
    },
    methods: {
        login () {
            if (!(/(.*)@weiche.cn$/.test(this.email))) {
                CommonTool.notify.notice('邮箱格式不正确')
                return false
            }
            window.localStorage.setItem('weiche_cms_email', this.email)
            CommonTool.ajaxPost(this.api + 'send_email', {email: this.email}, (res) => {
                window.location.href = 'http://mail.weiche.cn'
            }, (res) => {
                if (res.responseJSON.error === 'user_no_exists') {
                    CommonTool.notify.notice('登录邮箱不存在，请联系部门负责人', '错误信息', 'error')
                }
            })
        }
    }
}
</script>

<style type="text/css">
    .lock-screen {
        overflow: hidden;
    }
    .lock-wrapper {
        position: absolute;
        margin: 0px;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
    }
    .lock-wrapper .locked {
        left: 50%;
        margin-left: -25px;
    }
</style>



