/* global PNotify */
PNotify.prototype.options.styling = 'fontawesome'
var CommonTool = {
    modal: (function () {
        var $modal = $('#dialogModal')
        var _callback = null
        var _args = null
        var status = ''
        var isalert = false
        $modal.on('hidden.bs.modal', function () {
            if (status === 'alert.confirm') {
                _callback && _callback(_args)
            } else if (status === 'confirm.confirm') {
                _callback && _callback(true, _args)
            } else if (status === 'confirm.cancel') {
                _callback && _callback(false, _args)
            } else {
                if (!isalert) {
                    _callback && _callback(false, _args)
                }
            }
            _callback = null
            _args = null
            status = ''
        })

        $modal.find('._cancel').on('click', function () { status = 'confirm.cancel' })

        $modal.find('._confirm').on('click', function () { status = 'confirm.confirm' })

        $modal.find('._alert').on('click', function () { status = 'alert.confirm' })

        return {
            alert: function (body, callback, args) {
                isalert = true
                _callback = callback
                _args = args
                $modal.find('._cancel').hide()
                $modal.find('._confirm').hide()
                $modal.find('._alert').show()
                $modal.find('.modal-body').text(body)
                $modal.modal()
            },
            confirm: function (body, callback, args) {
                isalert = false
                _callback = callback
                _args = args
                $modal.find('._cancel').show()
                $modal.find('._confirm').show()
                $modal.find('._alert').hide()
                $modal.find('.modal-body').text(body)
                $modal.modal()
            }
        }
    }()),
    loadingStack: 0,
    loading: function (loadingText) {
        if (loadingText) {
            $('#loading-mask-div .loading-text').text(loadingText)
        } else {
            $('#loading-mask-div .loading-text').text('')
        }
        $('#loading-mask-div').fadeIn()
        this.loadingStack++
    },
    loaded: function () {
        this.loadingStack--
        if (this.loadingStack <= 0) {
            $('#loading-mask-div').fadeOut()
            this.loadingStack = 0
        }
    },
    loadedEever: function () {
        $('#loading-mask-div').fadeOut()
        this.loadingStack = 0
    },
    ajax: function (url, args, successFn, errorFn, showLoading, method) {
        showLoading = showLoading === undefined ? true : showLoading
        showLoading && this.loading()
        args = args === undefined ? '' : args
        successFn = successFn === undefined ? function () {} : successFn
        $.ajax({
            url: url,
            type: method,
            dataType: 'json',
            data: args
        })
        .done(successFn)
        .fail(function (res) {
            var signal = false
            var t = null
            if (errorFn) {
                errorFn(res)
                return
            } else if (res.status === 422) {
                t = JSON.parse(res.responseText)
                $.each(t, function (index, elem) {
                    signal = true
                    CommonTool.notify.notice(elem[0], false, 'error')
                })
            } else if (res.status === 500) {
                t = JSON.parse(res.responseText)
                if (t.msg === 'session_expired') {
                    if (window.location.href.indexOf('localhost') !== -1) {
                        window.location.href = '/single.html#/login'
                    } else {
                        window.location.href = '/weiche_cms/single.html#/login'
                    }
                }
                if (t.error && t.msg) {
                    signal = true
                    CommonTool.notify.notice(t.msg, t.error, 'error')
                }
            }
            if (!signal) {
                CommonTool.notify.notice('网络错误，请稍后再试', false, 'error')
            }
        })
        .always(function () {
            showLoading && CommonTool.loaded()
        })
    },
    ajaxGet: function (url, args, successFn, errorFn, showLoading) {
        this.ajax(url, args, successFn, errorFn, showLoading, 'GET')
    },
    ajaxPost: function (url, args, successFn, errorFn, showLoading) {
        this.ajax(url, args, successFn, errorFn, showLoading, 'POST')
    },
    notify: {
        notice: function (content, title, type) {
            title = title === undefined ? false : title
            content = content === undefined ? false : content
            type = type === undefined ? 'notice' : type
            var p = new PNotify({
                title: title,
                text: content,
                type: type,
                delay: 3000
            })
            // console.log(p)
            if (p) {

            }
        },
        remove: function () {
            PNotify.removeAll()
        }
    }

}
